    launchRocketship() {
        const rocketship = document.querySelector('.rocket');
        const exhaustFlames = document.querySelector('.exhaust-flame');

        rocketship.className += ' rocket-active';
        exhaustFlames.className += '-active';

        setTimeout(() => {
            rocketship.className = rocketship.className.replace(' rocket-active', '');
            exhaustFlames.className = exhaustFlames.className.replace('-active', '');
        }, 2500);
    }

    launchLink(link) {
        setTimeout(() => {
            const windowReference = window.open();
            windowReference.location = link;
        }, 1500);
    }

